<template>

<div>
    <div class="mt-3">
        <div class="mt-3">
            <h3><i class="fas fa-users" ></i> Clientes Agentes</h3>
        </div>
        <hr>

        <div class="row">
            <div class="form-group col-sm-12 col-md-3 mb-2">
                <label>Vendedor</label>
                <div class="input-group">
                    <b-form-select id="cboVendedor"
                                    v-model="agente.codVendedor"
                                    :options="vendedores"
                                    v-on:change="obtenerClientes"
                                    :disabled="disabledVendedor">
                    </b-form-select>
                </div>
            </div>

        </div>
        <!-- <br>
        <div name="divPrincipal">
            <div style="display: inline-grid;">
                <div class="table-responsive table-sm my-2 tableFixHead" style=" max-height: 440px;" >
                    <table class="table table-striped table-bordered">
                        <thead>
                            <tr>
                                <th style="width: 350px;">Nombre del cliente</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(cliente, index) in clientes" :key="index" >
                                <td v-text="cliente.nombreCompleto"></td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                
            </div>
        </div> -->

        <div>
                <hr>
                <b-row class="my-2">
                    <b-col md="4">
                        <b-form-group label="Filtrar"
                                    label-cols-sm="1"
                                    label-cols-md="2"
                                    label-size="sm"
                                    label-for="filterInput">
                            <b-input-group size="sm">
                                <b-form-input v-model="filter"
                                                type="search"
                                                id="filterInput"
                                                placeholder="Escribe el filtro..."
                                                autocomplete="off">
                                </b-form-input>
                                <b-input-group-append>
                                    <b-button :disabled="!filter" @click="filter = ''">
                                        <i class="fas fa-times"></i>
                                    </b-button>
                                </b-input-group-append>
                            </b-input-group>
                        </b-form-group>
                    </b-col>
                    <b-col md="3">
                        <b-form-group label="Por página"
                                        label-cols-sm="2"
                                        label-cols-md="2"
                                        label-cols-lg="4"
                                        label-size="sm"
                                        label-for="perPageSelect">
                            <b-form-select v-model="perPage"
                                            id="perPageSelect"
                                            size="sm"
                                            :options="pageOptions">
                            </b-form-select>
                        </b-form-group>
                    </b-col>
                </b-row>
                <!-- Main table element -->
                <b-table small
                        stacked="md"
                        head-variant="dark"
                        :hover="hover"
                        :striped="striped"
                        :bordered="bordered"
                        :items="items"
                        :fields="fields"
                        :current-page="currentPage"
                        :per-page="perPage"
                        :filter="filter"
                        :filterIncludedFields="filterOn"
                        :sort-by.sync="sortBy"
                        :sort-desc.sync="sortDesc"
                        :sort-direction="sortDirection"
                        @filtered="onFiltered">
                    <template v-slot:cell(estatusNuevo)="row">
                        <b-badge variant="success" v-if="row.item.estatusNuevo == 'A'" class="p-2">Activo</b-badge>
                        <b-badge variant="danger" v-if="row.item.estatusNuevo == 'DC'" class="p-2">Detenido Límite Crédito</b-badge>
                    </template>
                </b-table>
                
                <b-pagination v-model="currentPage"
                                class="mt-1"
                                :total-rows="totalRows"
                                :per-page="perPage"
                                align="center"
                                aria-controls="my-table">
                </b-pagination>
            </div>

	</div>
</div>

</template>

<script>

import servicioAgentes from '@/services/servicio-agentes';

export default {
    data(){
        return{
            vendedores: [{ value: 0, text: "Todos" }],
            clientes: [],
            agente: {
                codVendedor: 0,
            },
            disabledVendedor: false,





            datos: false,
            items: [],
            totalRows: 1,
            currentPage: 1,
            perPage: 5,
            pageOptions: [5, 10, 15],
            sortBy: "",
            sortDesc: false,
            bordered: true,
            hover: true,
            striped: true,
            sortDirection: "asc",
            filter: null,
            filterOn: [],
            fields: [
                {
                    key: "nombreComercial",
                    label: "Nombre",
                    sortable: true,
                    sortDirection: "desc",
                },
                {
                    key: "creditoFormat",
                    label: "Credito",
                    sortable: true,
                    class: "text-center",
                },
                {
                    key: "fechaCorta",
                    label: "Fecha Registro",
                    sortable: true,
                    sortDirection: "desc",
                },
                // {
                //     key: "correo",
                //     label: "correo",
                //     sortable: true,
                //     class: "text-center",
                // },
                {
                    key: "telefono",
                    label: "Telefono",
                    sortable: true,
                    class: "text-center",
                },
                { key: "estatusNuevo", label: "Estatus", sortable: true, class: "text-center" },

            ],


        }
    },
    components: {
    },
    mounted() {
    },

    async created() {

        console.log(this.usuario);

        if(this.usuario.agente === 1) {
            this.vendedores = [{ value: this.usuario.codEmpleado, text: this.usuario.nombre }];
            this.agente.codVendedor = this.vendedores[0].value;
            this.disabledVendedor = true;
            await this.obtenerClientes();
        } else {
            this.disabledVendedor = false;
            await this.obtenerVendedores();
        }
    },
    computed: {
        sortOptions() {
            // Create an options list from our fields
            return this.fields
            .filter((f) => f.sortable)
            .map((f) => {
            return { text: f.label, value: f.key };
            });
        },
    },
    methods: {

        async obtenerClientes() {
            this.items = [];
            this.$loading(true);
            await servicioAgentes.obtenerClientes(this.agente.codVendedor)
                .then(r=>{
                    console.log(r.data);
                    this.items = r.data;
                    // this.equipoSeleccionado = this.Equipos[0];
                    this.$loading(false);
                }).catch(error=>{console.log(error); this.$loading(false);});
        },

        // vendedores
        async obtenerVendedores() {
            this.vendedores = [{ value: 0, text: "Seleccione el vendedor" }];
            this.$loading(true);
            await servicioAgentes.agentesCombo().then((r) => {
                if (r.value) {
                    r.data.forEach((element) => {
                        let agente = {
                            value: element.codVendedor,
                            text: element.nombreVendedor,
                        };
                        this.vendedores.push(agente);
                    });
                }
            });
            this.$loading(false);
        },

        onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length;
            this.currentPage = 1;
        },

        CargarRenglones() {
            setTimeout(() => {
            this.totalRows = this.items.length;
            }, 1000);
        },

        
    }
}
</script>

<style scoped>


    table {
        border: 2px solid #5b9bd5;
        border-radius: 3px;
        background-color: #fff;
        /* width: 1000px; */
        }

    th {
        background-color: #5b9bd5;
        color: rgb(255, 255, 255);
        cursor: pointer;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        text-align: center;
    }

    .icon-edit {
		color: var(--color-blanco);
	}
    .icon-edit:hover {
        color:green;
        cursor: pointer;
	}

    .tableFixHead { overflow-y: auto; }

    .tableFixHead thead th { position: sticky; top: 0; }

</style>