import base from '@/services/servicio-base'

const servicio = {}

//............................................................ 
//                       A G E N T E S
//............................................................
servicio.agentesCombo = () => {
    return base.get(`/agentes/todosCombo`)
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}

servicio.obtenerPedidosAgente = () => {
    return base.get(`/agentes/pedidos`)
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}

servicio.obtenerClientes = (codAgente) => {
    return base.get(`/agentes/clientes/${codAgente}`)
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}

servicio.obtenerProductosXCliente = ( codCliente,codProducto ) => {
    return base.get(`/agentes/productos-cliente/${codCliente}/${codProducto}`)
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}

servicio.obtenerDetalleNotasRemisionAgente = (idNotaRemision) => {
    return base.get(`/agentes/notas-remision-detalle/${idNotaRemision}`)
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}

servicio.guardarPrecioProducto = (producto) => {
    return base.post("/agentes/guardar-precio-productoXCliente", producto, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(r => {
            return r.data
        })
        .catch(err => {
            console.log(err)
        })
}

export default servicio